
.body-wrapper{
    background-color: rgb(65, 65, 65);
    color: white;
}



.home-template .main{
    padding-left: 150px;
    padding-top: 50px;
    padding-right: 35px;
    color: white;
 
}

.home-template .nav-left{
    background-color: rgb(45, 45, 45);
    padding-top: 20px;
}

.post-card-content-link{
    color: white;
}

.post-card-content-link:hover{
    color: rgb(31, 182, 219);
}

.side-nav ul{
    list-style: none;
    padding-left: 0px;
    text-align: center;
}

.side-nav .logo{
    width: 100%;
}

.side-nav .blog-title{
    text-align: center;
    font-size: 45px;
}

.side-nav .author-name{
    text-align: center;
    font-size: 13px;
}

.side-nav .blog-description{
    font-size: 15px;
    text-align: center;
}

.side-nav .blog-title-only, .side-nav .author-name-only{
    text-align: center;
    font-size: 60px;
}